import React, {Component} from "react";
import PropTypes from "prop-types";

const PriceBox = (props) => {
    if (!props.price || !props.price.monthlyCosts || !props.price.monthlyCosts.length > 0 || !props.price.monthlyCosts[0]){
        return null;
    }
    const price = !!props.net ? props.price.monthlyCosts[0].net.split(',') : props.price.monthlyCosts[0].gross.split(',');
    const integerPricePart = price[0];
    const fractionPricePart = price[1].split(' ')[0];
    const monthly = !!props.price.monthlyCosts[0].monthFrom || !!props.price.monthlyCosts[0].monthTo;
    const currency = `${props.price.monthlyCosts[0].currency}${monthly && '/mies.'}`;
    return (
        <div>
            <span className="opl-price-v2">
                <span className="opl-price-v2__part u-padding-right-s">
                    <span
                        className="opl-price-v2__whole">{integerPricePart}</span>
                    </span>
                    <span className="opl-price-v2__part">
                    <span className="opl-price-v2__separator">,</span>
                    <span className="opl-price-v2__decimal">{fractionPricePart}</span>
                    <span className="opl-price-v2__suffix">{currency}</span>
                </span>
            </span>
            <div>
                {
                    props.price.oneTimeCost &&
                    <div className="u-spacing-top-s u-font-small u-font-bold">
                        +&nbsp;{!!props.net ? props.price.oneTimeCost.net : props.price.oneTimeCost.gross} {props.price.oneTimeCost.currency}&nbsp;jednorazowa op≈Çata
                    </div>
                }
            </div>
        </div>
    )
}

PriceBox.propTypes = {
    price: PropTypes.shape({
        oneTimeCost: PropTypes.shape({
            gross: PropTypes.string,
            net: PropTypes.string,
            currency: PropTypes.string
        }),
        monthlyCosts: PropTypes.arrayOf(
            PropTypes.shape({
                gross: PropTypes.string,
                net: PropTypes.string,
                currency: PropTypes.string,
                monthFrom: PropTypes.number,
                monthTo: PropTypes.number
            })
        )
    }),
}

export default PriceBox;